<h1 id="intellijieda" tabindex="-1"><a class="header-anchor" href="#intellijieda" aria-hidden="true">#</a> IntellijIEDA</h1>
<h2 id="激活方法" tabindex="-1"><a class="header-anchor" href="#激活方法" aria-hidden="true">#</a> 激活方法</h2>
<p><strong>jar包激活</strong></p>
<p>找到<code>/Users/lionel/Library/Preferences/PhpStorm2019.1/phpstorm.vmoptions</code>路径</p>
<p>填写如下内容</p>
<div class="language-yaml ext-yml line-numbers-mode"><pre v-pre class="language-yaml"><code><span class="token punctuation">-</span>javaagent<span class="token punctuation">:</span>/Applications/PhpStorm.app/Contents/bin/jetbrains<span class="token punctuation">-</span>agent.jar
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="最新激活方法" tabindex="-1"><a class="header-anchor" href="#最新激活方法" aria-hidden="true">#</a> 最新激活方法</h2>
<p>使用插件<code>IDE Eval Reset</code></p>
<p>https://zhile.io/2020/11/18/jetbrains-eval-reset.html</p>
